import axios from 'axios';
import * as constants from './constants';

const changeComment = (result) => ({
	type: constants.CHANGECOMMENT,
	value: result.comment
})

export const getComment = (bookid) => {
	return (dispatch) => {
		axios.post('/bookComment?bookid=' + bookid).then((res) => {

			const data = res.data.data;
			const result=res.data.msg;
			if (result==="SUCCESS") {
				dispatch(changeComment(data));
			
			}else {
				alert('登陆失败')
			}
		})
	}
}